.FrameApplicationUI__wrapper{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: #fefcfb;
    .FAUI__top{
        flex: 1;
        width: 100%;
    }
    .FAUI__bottom{
        flex: 6;
        width: 100%;
        min-height: 85.7%;
        display: flex;
        .FAUI__bottom__left{
            flex: 1;
            padding: 1em;
            overflow: auto;
            box-shadow: 5px 0 3px -3px #00000030;
        }
        .FAUI__bottom__right{
            flex: 3;
        }
    }
}